import React from 'react';
import Heading from '../Heading/Heading';
import Input from '../Inputs/Input';
import Output from '../Outputs/Output';

const todo = () => {
    return(
        <div className="todo-flex">
            <Heading/>
            <Output/>
            <Input/>
        </div>
    )
}

export default todo;


