import React from 'react';
import './Help.css';

const help = () => {
    return (
        <div className="info-container">
            <h5 style={{ color: 'grey' }} className="help-title">About</h5>
            <p>Thanks for using this app :) <br/>
                It is a simple TODO app with scoring.<br/>
                Scoring is based on priority of the tasks.<br/>
                You can also check logs by clicking scores.
            </p>
            <table className="scoring-table">
                <tr>
                    <th className="head">Completed</th>
                    <th className="head">Deleted</th>
                </tr>
                <tr>
                    <td className="high" >High: +3</td>
                    <td className="high" >High: -3</td>
                </tr>
                <tr>
                    <td className="mid" >Medium: +2</td>
                    <td className="mid" >Medium: -2</td>
                </tr>
                <tr>
                    <td className="low" >Low: +1</td>
                    <td className="low" >Low: -1</td>
                </tr>
            </table>
        </div>
    )
}

export default help;


