import React, { Component } from 'react'
import './Output.css';
import Task from './Tasks/Tasks';
import { connect } from 'react-redux';
import { priorityColor, sortedList } from '../../Functions/Functions'

class output extends Component {

    render() {

        let tasksArray = sortedList(this.props.tasks)
    
        let pendingTasks = tasksArray.map((task, index) => {
            let c = priorityColor(task)
            return (
                <Task
                    complete={() => this.props.complete(index)}
                    priorityColor={c}
                    isOpenHandler={() => this.props.isTaskOpen(index)}
                    deleteTask={() => this.props.deleteTask(index)}
                    key={index}
                    name={task.taskName}
                    des={task.des}
                    date={task.date}
                    time={task.time}
                    isDone={task.isDone}
                    isOpen={task.isOpen}
                />
            )
        })

        let completed = this.props.completed.map((task, index) => {
            let c = '#cacaca';
            const backStyle = {
                borderLeft: `7px solid ${c}`,
                color: `${c}`,
                textDecoration: 'line-through'

            }
            return (
                <Task
                    undo={() => this.props.undo(index)}
                    backStyle={backStyle}
                    isDone={task.isDone}
                    isOpen={task.isOpen}
                    priorityColor={c}
                    key={index}
                    name={task.taskName}
                />
            )
        })

        completed.reverse();

        return (
            <div className="output-container">
                <div className="pending-tasks">
                    {pendingTasks}
                </div>
                <div className="completed-tasks">
                    <div style={{
                        display: 'flex',
                        flexDirection: 'row',
                        justifyContent: 'space-between'
                    }}>
                        {this.props.tasks.length <= 0 ? <p className="all-completed">No Pending Tasks :)</p> : <p className="all-completed"></p>}
                        {this.props.completed.length > 0 ? <p onClick={this.props.clearAll} className="clear-all">Clear all</p> : null}
                    </div>
                    {completed}
                </div>

            </div>
        )



    }


}

const mapStateToProps = state => {
    return {
        tasks: state.tasks,
        completed: state.tasksCompleted
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        deleteTask: (i) => dispatch({ type: 'DELETE', index: i }),
        isTaskOpen: (i) => dispatch({ type: 'OPEN', index: i }),
        complete: (i) => dispatch({ type: 'COMPLETE', index: i }),
        undo: (i) => dispatch({ type: 'UNDO', index: i }),
        clearAll: () => dispatch({ type: 'CLEAR' })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(output);


