import React, { Component } from 'react'

import './Input.css';
import { getCurrentDate } from '../../Functions/Functions'
import { connect } from 'react-redux';
import { Input, Collapse } from 'reactstrap';


class input extends Component {
    render() {

        let taskName = '';
        let descriptionValue = '';
        let priorityValue = 0;
        let defaultTaskNameValue = null;
        let defaultdescriptionValue = null;
        let defaultpriorityValue = null;

        console.log('IsAddnewOpen', this.props.isAddNewOpen)
        if (this.props.isAddNewOpen === false) {
            defaultTaskNameValue = taskName;
            defaultdescriptionValue = descriptionValue;
            defaultpriorityValue = priorityValue;
        }

        const taskNameValue = (event) => {
            taskName = event.target.value;
        }

        const description = (event) => {
            descriptionValue = event.target.value;
        }

        const priority = (event) => {
            priorityValue = Number(event.target.value);
        }

        console.log(typeof (priorityValue))
        console.log(priorityValue)

        return (
            <div className="input-container-super">

                <Collapse className="collapseUp" isOpen={this.props.isAddNewOpen}>
                    <div className="formstyle">
                        <div style={{
                            display: 'flex',
                            flexDirection: 'row',
                            justifyContent: 'space-between'
                        }}>
                            <Input style={{
                                width: '49%',
                                border: '0.5px solid #c9c9c9',

                                borderRadius: '7px'
                            }} value={defaultTaskNameValue} placeholder="Title" onChange={taskNameValue} type="text" name="title" required={true}></Input>

                            <Input style={{
                                width: '46%',

                                borderStyle: 'none',
                                border: '0.5px solid #c9c9c9',
                                borderRadius: '7px'
                            }} value={defaultpriorityValue} required={true} onChange={priority} type="select" name="select">
                                <option value="4">Priority</option>
                                <option className="high" value="1">High</option>
                                <option className="mid" value="2">Medium</option>
                                <option className="low" value="3">Low</option>
                            </Input>
                        </div>
                        <textarea value={defaultdescriptionValue} placeholder="Description" className="des" onChange={description}></textarea>

                    </div>

                </Collapse>

                <div className="hint-container">
                    {/* <p className="hint">{this.props.message}</p> */}
                    <button className="add-button" onClick={() => this.props.addTask(priorityValue, taskName, descriptionValue)} >+</button>
                </div>
            </div >
        )
    }
}






const mapStateToProps = state => {
    return {
        message: state.message,
        isAddNewOpen: state.isAddNewOpen
    }
}

const mapDispatchToProps = (dispatch) => {

    return {
        addTask: (p, n, d) => dispatch({
            type: 'ADD',
            task: {
                priority: p,
                taskName: n,
                des: d,
                isDone: false,
                date: getCurrentDate(),
                time: new Date().toTimeString().split(' ')[0]
            }
        })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(input);



