import React from 'react'
import './Log.css';
import { priorityColor } from '../../../Functions/Functions'

const log = (props) => {

    const style = {borderLeft: `7px solid ${priorityColor(props)}`}
    return (
        <div className="task-log-container horizontal-flex">
            <p style={style} className="task-log">{props.name} <em>{props.status}</em> {props.score} </p>
            <div className="vertical-flex">
                <p className="time-date"><em>{props.date}</em></p>
                <p className="time-date"><em>{props.time}</em></p>
            </div>
        </div>
    )

}
export default log;


