import { scoreCalculator, saveToLocalStorage, loadFromLocalStorage, getCurrentDate } from '../Functions/Functions';

let initialState = {
    score: 0,
    tasks: [],
    tasksCompleted: [],
    message: 'Add new task!',
    isHelpOpen: false,
    isAddNewOpen: false,
    isLogOpen: false,
    logs: []    //Priority, Score, Name, Status, Time, Date

};

loadFromLocalStorage() !== undefined ? initialState = loadFromLocalStorage() : initialState;

const reducers = (state = initialState, action) => {

    if(action.type === 'CLEAR_LOGS'){
    
        let s = {
            ...state,    
            logs: []
        };
        
        saveToLocalStorage(s);
        return s;

    }

    if (action.type === 'ADD') {

        let tasksTEMP = [...state.tasks];
        let logsTEMP = [...state.logs];

        const isProperConditionToOpenInputField = state.isAddNewOpen === true && action.task.priority !== 4 && action.task.priority !== 0 && action.task.taskName !== '';
        const isDescriptionEmpty = action.task.des === '' || action.task.des === null;

        if (isProperConditionToOpenInputField) {
            if (isDescriptionEmpty) {
                action.task.des = 'No Description Available.'
            }

            let log = {
                priority: action.task.priority,
                score: 0,
                name: action.task.taskName,
                status: 'Added',
                time: action.task.time,
                date: action.task.date
            }
            logsTEMP.push(log)

            tasksTEMP.push(action.task)
        }

        let isAddNewOpenTEMP;
        const isHeadingToggleNotOpen = state.isLogOpen === false && state.isHelpOpen === false;
        if (isHeadingToggleNotOpen) {
            isAddNewOpenTEMP = !state.isAddNewOpen;
        } else {
            isAddNewOpenTEMP = state.isAddNewOpen;
        }

        let s = {
            ...state,
            isAddNewOpen: isAddNewOpenTEMP,
            tasks: tasksTEMP,
            logs: logsTEMP
        };
        
        saveToLocalStorage(s);
        return s;
    }

    if (action.type === 'LOG') {
        if (!state.isHelpOpen) {
            let isLogOpenTEMP = !state.isLogOpen;
            let s = {
                ...state,
                isLogOpen: isLogOpenTEMP
            };
            saveToLocalStorage(s);
            return s;
        }
    }

    if (action.type === 'UNDO') {

        let tasksCompletedTEMP = [...state.tasksCompleted];
        let logsTEMP = [...state.logs];

        tasksCompletedTEMP[action.index].isDone = false;
        let undoTask = tasksCompletedTEMP.splice(action.index, 1);
        let key = '-'
        let negativeScore = scoreCalculator(state, undoTask[0], key);

        let log = {
            priority: undoTask[0].priority,
            score: `-${negativeScore}`,
            name: undoTask[0].taskName,
            status: 'Undo',
            time: new Date().toTimeString().split(' ')[0],
            date: getCurrentDate()
        }
        logsTEMP.push(log)

        let s = {
            ...state,
            tasksCompleted: tasksCompletedTEMP,
            score: state.score - negativeScore,
            tasks: state.tasks.concat(undoTask),
            logs: logsTEMP
        };
        
        saveToLocalStorage(s);
        return s;
    }

    if (action.type === 'HELP') {
        if (!state.isLogOpen) {
            let isHelpOpenTEMP = !state.isHelpOpen;
            let s = {
                ...state,
                isHelpOpen: isHelpOpenTEMP
            };
            saveToLocalStorage(s);
            return s;
        }
    }

    if (action.type === 'CLEAR') {
        let s = {
            ...state,
            tasksCompleted: []
        };
        saveToLocalStorage(s);
        return s;
    }

    if (action.type === 'COMPLETE') {
        let tasksTEMP = [...state.tasks];
        let logsTEMP = [...state.logs];

        tasksTEMP[action.index].isDone = true;
        let completedTask = tasksTEMP.splice(action.index, 1);
        let key = '+';
        let positiveScore = scoreCalculator(state, completedTask[0], key);

        let log = {
            priority: completedTask[0].priority,
            score: `+${positiveScore}`,
            name: completedTask[0].taskName,
            status: 'Completed',
            time: new Date().toTimeString().split(' ')[0],
            date: getCurrentDate()
        }
        logsTEMP.push(log)

        let s = {
            ...state,
            tasks: tasksTEMP,
            score: state.score + positiveScore,
            tasksCompleted: state.tasksCompleted.concat(completedTask),
            logs: logsTEMP
        };

        
        saveToLocalStorage(s);
        return s;
    }

    if (action.type === 'OPEN') {
        let tasksTEMP = [...state.tasks];
        tasksTEMP[action.index].isOpen = !tasksTEMP[action.index].isOpen;
        let s = {
            ...state,
            tasks: tasksTEMP
        };
        saveToLocalStorage(s);
        return s;
    }

    if (action.type === 'DELETE') {
        let tasksTEMP = [...state.tasks];
        let logsTEMP = [...state.logs];

        let deletedTask = tasksTEMP.splice(action.index, 1);
        let key = '-'
        let negativeScore = scoreCalculator(state, deletedTask[0], key);

        let log = {
            priority: deletedTask[0].priority,
            score: `-${negativeScore}`,
            name: deletedTask[0].taskName,
            status: 'Deleted',
            time: new Date().toTimeString().split(' ')[0],
            date: getCurrentDate()
        }
        logsTEMP.push(log)

        let s = {
            ...state,
            score: state.score - negativeScore,
            tasks: tasksTEMP,
            logs: logsTEMP
        };
        console.log('state-deleted', s)
        saveToLocalStorage(s);
        return s;
    }

    return state;
};

export default reducers;


