import React from 'react';

import './Tasks.css';
import { Collapse } from 'reactstrap';
import UntickSVG from '../../../Assets/Icons/untick.svg';
import DeleteSVG from '../../../Assets/Icons/delete.svg';
import tickSVG from '../../../Assets/Icons/tick.svg';

const task = (props) => {

    // let date = props.date
    // date.shift()
    // date = date.join('-')
    

    let fontStyle = null;
    if (props.isDone) {
        fontStyle = {
            textDecoration: 'line-through',
            color: 'grey'
        }
    }

    let tickButton = null;
    if (props.isDone) {
        tickButton = <button style={{

            backgroundImage: `url(${tickSVG})`,
            backgroundRepeat: 'no-repeat',
            backgroundPosition: 'center'

        }} onClick={props.undo} className="task-button"></button>
    } else if (props.isOpen) {
        tickButton = <button style={{

            backgroundImage: `url(${DeleteSVG})`,
            backgroundRepeat: 'no-repeat',
            backgroundPosition: 'center'

        }} onClick={props.deleteTask} className="task-button"></button>

    } else {
        tickButton = <button style={{

            backgroundImage: `url(${UntickSVG})`,
            backgroundRepeat: 'no-repeat',
            backgroundPosition: 'center'

        }} onClick={props.complete} className="task-button"></button>

    }

    return (
        <div>
            <div
                style={{
                    borderLeft: `7px solid ${props.priorityColor}`
                }}
                className="task-container" >
                <div style={fontStyle} onClick={props.isOpenHandler} className="task-line">
                    {props.name}
                </div>
                <div className="controls">
                    {tickButton}
                </div>
            </div>

            <Collapse isOpen={props.isOpen}>
                <div style={{
                    backgroundColor: `#efefef`
                }} className="details">
                    
                    <div>{props.des}</div>
                    <div>
                        <div className="time-date"><em>{props.date}</em></div>
                        <div className="time-date"><em>{props.time}</em></div>
                    </div>
                    
                    
                
                </div>
            </Collapse>
        </div>
    )
}

export default task;


