import React from 'react'
import './Heading.css';
import { connect } from 'react-redux';
import { Collapse, Col } from 'reactstrap';
import Log from './Logs/Log';
import Help from './Help/Help';
const heading = (props) => {

    let logsList = props.logsData.map(log => <Log


        priority={log.priority}
        score={log.score}
        name={log.name}
        status={log.status}
        time={log.time}
        date={log.date}

    />


    )

    logsList.reverse();

    return (
        
        <div className="super-heading">

            <div className="heading">
                <h1 onClick={props.isHelp} className="title">Todo</h1>
                <h2 onClick={props.logs} className="score">{props.score}</h2>
            </div>

            <Collapse className="heading-collapse" isOpen={props.isHelpOpen}>
                <Help />
            </Collapse>

            <Collapse className="heading-collapse" isOpen={props.isLogOpen}>
                <div className="info-container">

                    {
                        props.logsData.length === 0 ?
                            <p>No Logs Available</p> :
                            <div>
                                <h6 onClick={props.clearLogs} className="clear-log">Clear Logs</h6>
                                {logsList}
                            </div>
                    }

                </div>
            </Collapse>
        </div>
    )
}
const mapStateToProps = state => {
    return {
        score: state.score,
        isHelpOpen: state.isHelpOpen,
        isLogOpen: state.isLogOpen,
        logsData: state.logs
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        isHelp: () => dispatch({ type: 'HELP' }),
        logs: () => dispatch({ type: 'LOG' }),
        clearLogs: () => dispatch({ type: 'CLEAR_LOGS' })
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(heading);


